import React from "react";
import PlainSelect from "../../../util/PlainSelect";
import { Form, Input } from "antd";
const { TextArea } = Input;

export default function referenceEditForm({ essence }) {
  const reference = window.appConfig?.adminReferences[essence] || {};

  let formItems = {
    name: {
      rules: [
        {
          required: true,
        },
      ],
    },

    type: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("typename"),
      component: (
        <PlainSelect labelKey="name" cache query={`/${essence}/type`} />
      ),
    },

    description: {
      component: <TextArea />,
    },
  };

  if (reference.extraFields) {
    Object.entries(reference.extraFields).forEach(([key, field]) => {
      formItems[key] = {
        label: this.m(key),
      };
      if (field.type === "reference") {
        formItems[key].component = (
          <PlainSelect labelKey="name" cache query={field.path} />
        );
      }
      if (field.required) {
        formItems[key].rules = [
          {
            required: true,
          },
        ];
      }
    });
  }

  return (
    <>
      {Object.entries(formItems).map(([key, item]) => {
        let label = item.label || this.m(key);
        let component = item.component || <Input />;

        return (
          <Form.Item key={key} rules={item.rules} label={label} name={key}>
            {component}
          </Form.Item>
        );
      })}
    </>
  );
}
